"use client";

import React, { useState } from "react";
import {
  Box,
  Text,
  Image,
  Button,
  VStack,
  HStack,
  Badge,
  useColorMode,
  useColorModeValue,
} from "@chakra-ui/react";
import { FaHeart, FaTimes, FaComment } from "react-icons/fa";
import { motion, AnimatePresence } from "framer-motion";
import { useRouter } from "next/navigation";
import homeUsers from "@/data/homeUsers";
import useColorModeStyles from "@/utils/useColorModeStyles";

const MotionBox = motion(Box);

interface User {
  id: string;
  username: string;
  firstName: string;
  age: number;
  location: string;
  bio: string;
  interests: string[];
  imageUrl: string;
}

const MainHome: React.FC = () => {
  const [currentIndex, setCurrentIndex] = useState<number>(0);
  const [exitAnimation, setExitAnimation] = useState<any>({});
  const router = useRouter();

  // Get the theme styles from the custom hook
  const {
    bg,
    textColor,
    borderColor,
    hoverColor,
    navBgColor,
    instagramGradient,
    instagramTextColor,
    instagramIconColor,
    toggleColorMode,
  } = useColorModeStyles(); // Destructure theme values

  const handleNextUser = () => {
    setCurrentIndex((prevIndex) => (prevIndex + 1) % homeUsers.length);
  };

  const handleIgnore = () => {
    setExitAnimation({
      border: "2px solid red",
      opacity: 0,
      y: 300,
    });
    setTimeout(() => {
      setExitAnimation({});
      handleNextUser();
    }, 500);
  };

  const handleLike = () => {
    setExitAnimation({
      border: "2px solid green",
      opacity: 0,
      y: 900,
    });
    setTimeout(() => {
      setExitAnimation({});
      handleNextUser();
    }, 500);
  };

  const handleChat = () => {
    const { username } = currentUser;
    router.push(`/chat/${username}`);
  };

  const currentUser: User = homeUsers[currentIndex];

  return (
    <Box
      display="flex"
      justifyContent="center"
      alignItems="center"
      height="100%"
      p={4}
      bg={bg} // Apply background color based on theme
    >
      <AnimatePresence>
        <MotionBox
          key={currentUser.id}
          initial={{ opacity: 0, x: 300 }}
          animate={{ opacity: 1, x: 0 }}
          exit={exitAnimation}
          transition={{ duration: 0.5 }}
          maxW="400px"
          borderWidth="1px"
          borderRadius="lg"
          overflow="hidden"
          boxShadow="lg"
          bg={bg} // Apply background color based on theme
          borderColor={borderColor} // Apply border color based on theme
        >
          {/* User Image */}
          <Image
            src={currentUser.imageUrl}
            alt={currentUser.username}
            objectFit="cover"
            width="100%"
            height="350px"
          />

          {/* User Details */}
          <Box p={4}>
            <Text fontSize="xl" fontWeight="bold" color={textColor}>
              {currentUser.firstName}, {currentUser.age}
            </Text>
            <Text fontSize="sm" color="gray.500">
              {currentUser.location}
            </Text>
            <Text mt={2} noOfLines={2} color={textColor}>
              {currentUser.bio.length > 50
                ? `${currentUser.bio.substring(0, 50)}...`
                : currentUser.bio}
            </Text>
            <HStack spacing={2} mt={2}>
              {currentUser.interests.map((interest) => (
                <Badge key={interest} colorScheme="teal">
                  {interest}
                </Badge>
              ))}
            </HStack>
          </Box>

          {/* Action Buttons */}
          <HStack justifyContent="space-around" p={4}>
            <Button
              leftIcon={<FaHeart />}
              colorScheme="green"
              onClick={handleLike}
              _hover={{ bg: hoverColor }} // Apply hover color based on theme
            >
              Like
            </Button>
            <Button
              leftIcon={<FaComment />}
              colorScheme="blue"
              onClick={handleChat}
              _hover={{ bg: hoverColor }} // Apply hover color based on theme
            >
              Chat
            </Button>
            <Button
              leftIcon={<FaTimes />}
              colorScheme="red"
              onClick={handleIgnore}
              _hover={{ bg: hoverColor }} // Apply hover color based on theme
            >
              Ignore
            </Button>
          </HStack>
        </MotionBox>
      </AnimatePresence>
    </Box>
  );
};

export default MainHome;
