import React, { useEffect, useState } from "react";
import {
  Flex,
  IconButton,
  Menu,
  MenuButton,
  MenuList,
  MenuItem,
  Tooltip,
  Link,
  AlertDialog,
  AlertDialogOverlay,
  AlertDialogContent,
  AlertDialogHeader,
  AlertDialogBody,
  AlertDialogFooter,
  Button,
} from "@chakra-ui/react";
import { BellIcon, SettingsIcon, MoonIcon, SunIcon } from "@chakra-ui/icons";
import { FaUser } from "react-icons/fa";
import { IoLogOut } from "react-icons/io5";
import { HiDocumentText } from "react-icons/hi";
import { MdPrivacyTip } from "react-icons/md";
import logo from "../../public/logo.png";
import Image from "next/image";
import useColorModeStyles from "@/utils/useColorModeStyles";

const Header = () => {
  const { bg, toggleColorMode } = useColorModeStyles();
  const [username, setUsername] = useState("User");
  const { hoverColor } = useColorModeStyles();

  const [isOpen, setIsOpen] = useState(false);
  const cancelRef = React.useRef();

  useEffect(() => {
    // axiosInstance
    //   .get("/api/header-data/")
    //   .then((response) => {
    //     const userData = response.data;
    //     console.log(response.data);
    //     setUsername(userData.username);
    //   })
    //   .catch((error) => {
    //     console.log("Error fetching header data", error);
    //   });
  }, []);

  const onOpen = () => setIsOpen(true);
  const onClose = () => setIsOpen(false);

  const handleLogout = () => {
    // Handle logout logic here
    console.log("Logged out");
    onClose();
  };

  return (
    <Flex justify="space-between" align="center" p={4} bg={bg} h="100%">
      <Image height={28} src={logo} alt="Logo" />
      <Flex align="center">
        <Tooltip label="Notifications" aria-label="Notifications">
          <Link href="/notifications">
            <IconButton
              icon={<BellIcon />}
              aria-label="Notifications"
              variant="ghost"
              _hover={{ color: hoverColor }}
              mr={4}
            />
          </Link>
        </Tooltip>
        <Menu>
          <MenuButton
            as={IconButton}
            icon={<FaUser />}
            aria-label="Profile"
            variant="ghost"
          />
          <MenuList>
            <MenuItem>Welcome {username}</MenuItem>
            <MenuItem icon={<FaUser />}>
              <Link href="/profile">Profile</Link>
            </MenuItem>
            <MenuItem icon={<HiDocumentText />}>
              <Link href="/termsofservice">Terms of Service</Link>
            </MenuItem>
            <MenuItem icon={<MdPrivacyTip />}>
              <Link href="/privacypolicy">Privacy Policy</Link>
            </MenuItem>
            <MenuItem onClick={onOpen} icon={<IoLogOut style={{ transform: "scaleX(-1)" }} />}>
              Log Out
            </MenuItem>
          </MenuList>
        </Menu>
        <IconButton
          icon={bg === "gray.200" ? <MoonIcon /> : <SunIcon />}
          aria-label="Toggle Color Mode"
          variant="ghost"
          onClick={toggleColorMode}
          ml={4}
        />
      </Flex>

      {/* Logout Confirmation Dialog */}
      <AlertDialog isOpen={isOpen} leastDestructiveRef={cancelRef} onClose={onClose}>
        <AlertDialogOverlay>
          <AlertDialogContent>
            <AlertDialogHeader fontSize="lg" fontWeight="bold">
              Confirm Logout
            </AlertDialogHeader>
            <AlertDialogBody>Are you sure you want to log out?</AlertDialogBody>
            <AlertDialogFooter>
              <Button ref={cancelRef} onClick={onClose}>Cancel</Button>
              <Button colorScheme="red" onClick={handleLogout} ml={3}>Log Out</Button>
            </AlertDialogFooter>
          </AlertDialogContent>
        </AlertDialogOverlay>
      </AlertDialog>
    </Flex>
  );
};

export default Header;
